@supports not (font-variation-settings: normal) {
  @font-face {
    font-family: "Unbounded";
    src: url(../assets/fonts/Unbounded/static/Unbounded-Regular.ttf) format("truetype");
    font-weight: 400; /* Regular weight */
    font-display: swap; /* Ensure font swap is applied */
  }
  @font-face {
    font-family: "Unbounded";
    src: url(../assets/fonts/Unbounded/static/Unbounded-Bold.ttf) format("truetype");
    font-weight: 700; /* Bold weight */
    font-display: swap;
  }
  @font-face {
    font-family: "Karla";
    src: url(../assets/fonts/Karla/static/Karla-Regular.ttf) format("truetype");
    font-weight: 400; /* Regular weight */
    font-display: swap;
  }
  @font-face {
    font-family: "Karla";
    src: url(../assets/fonts/Karla/static/Karla-Regular.ttf) format("truetype");
    font-weight: 700; /* Bold weight */
    font-display: swap;
