h1,
h2,
h3,
h4,
h5,
h6,
ol,
ul,
a,
p,
b,
small,
button,
span,
figcaption,
time {
  color: #f0e9f2;
  line-height: 125%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Unbounded", sans-serif;
  font-weight: 400;
  letter-spacing: 2px;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.5rem;
}

h3 {
  font-size: 1.125rem;
}

ol,
ul,
a,
p,
b,
button,
span,
figcaption,
time {
  font: 400 1.125rem/125% "Karla", sans-serif;
  color: #f0e9f2;
  letter-spacing: 1px;
}

p,
b,
figcaption {
  font-weight: 300;
}

small {
  font-family: "Karla", sans-serif;
}

time {
  font-size: 1rem;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ol,
ul {
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
}

address {
  font-style: normal;
}

html {
  scroll-behavior: smooth;
}

footer {
  background-color: #363d48;
}

.primary-btn {
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  gap: 10px;
  border: 1px solid #f22c50;
  padding: 12px 24px;
  font-size: 1.125rem;
}
.primary-btn img {
  height: 1.25rem;
  width: 1.25rem;
}
.primary-btn:hover,
.primary-btn :active {
  transition: all 0.3s ease-in-out;
  filter: opacity(0.7);
}

.section {
  max-width: 1400px;
  margin: 0 auto;
  padding: 4rem 1rem;
}

.primary-colour {
  background-color: #22252c;
}

.asset-colour {
  background-color: #363d48;
}

.spacing-4 {
  margin: 4rem 0 0 0;
}

.section-heading {
  display: flex;
  flex-direction: column;
  max-width: fit-content;
  position: relative;
}

.heading-underline {
  display: inline-block;
  border-bottom: 1px solid #f22c50;
  width: 50%;
  margin-top: 0.5rem;
}

/* --- hamburger menu --- */
.hamburger,
.mobile-nav {
  display: none;
}

@media (width <= 800px) {
  .hamburger {
    display: block;
    position: relative;
    width: 1.625rem;
    cursor: pointer;
    outline: none;
    border: none;
  }
  .hamburger-bar,
  .hamburger::after,
  .hamburger::before {
    content: "";
    display: block;
    height: 1px;
    width: 100%;
    margin: 6px 0;
    background-color: #f0e9f2;
    transition: 0.3s ease-in-out;
  }
  .hamburger.is-active::after,
  .hamburger.is-active::before {
    background-color: #f0e9f2;
  }
  .hamburger.is-active .hamburger-bar {
    opacity: 0;
  }
  .hamburger.is-active:before {
    transform: rotate(-45deg) translate(-5px, 5px);
  }
  .hamburger.is-active:after {
    transform: rotate(45deg) translate(-5px, -5px);
  }
  .mobile-nav {
    position: fixed;
    display: flex;
    flex-direction: column;
    top: 0;
    left: 100%;
    width: 100%;
    height: 100vh;
    background-color: #22252c;
    padding-top: 137px;
    z-index: 98;
    transition: 0.3s ease-in-out;
  }
  .mobile-nav.is-active {
    left: 0;
  }
}
.landing-section {
  background-color: #22252c;
}

.outer-header-container {
  height: 100vh;
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 1rem;
  background: no-repeat url("../assets/images/profile1.webp") 100% center/60% auto;
  background-color: #22252c;
  background-blend-mode: luminosity;
  overflow: hidden;
  position: relative;
  z-index: 1;
}

.header-bar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0;
  position: relative;
  z-index: 100;
}

.logo-container {
  color: #f0e9f2;
}

.links-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.page-link-wrapper {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.main-link {
  position: relative;
  padding: 5px 0.5rem;
}

.main-link::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0%;
  height: 1px;
  background-color: #f22c50;
  transition: width 0.3s ease-in-out;
}

.main-link:hover::before {
  width: 100%;
}

.main-link:active::before {
  background-color: #f0e9f2;
}

.page-link-wrapper a {
  font-size: 1.25rem;
}

.some-links-wrapper {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.some-links-wrapper img {
  display: block;
  height: 20px;
  width: 20px;
}

.some-link:hover,
.some-link:active {
  filter: opacity(0.7);
}

.utility-btn-wrapper {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.colour-mode-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
}
.colour-mode-wrapper button {
  line-height: 0;
}
.colour-mode-wrapper p {
  font-size: 0.75rem;
}

.light-mode-btn:hover,
.light-mode-btn:hover {
  filter: opacity(0.7);
}

@media (width <= 1000px) {
  .header-bar-container nav:last-of-type {
    display: none;
  }
  .page-link-wrapper .main-link {
    font-size: 1rem;
  }
}
@media (width <= 800px) {
  .header-bar-container nav,
  .colour-mode-wrapper p {
    display: none;
  }
  .colour-mode-wrapper img {
    width: 27px;
    height: 27px;
  }
  .outer-header-container {
    background-position: 150% 100%;
  }
}
