.intro-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;
  gap: 2rem;
  max-width: 90ch;
  position: relative;
  top: 50%;
  transform: translate(0, -70%);
}

.name-container {
  display: flex;
  align-items: center;
  justify-self: center;
  color: #f0e9f2;
  margin: 0 0 0.5rem 0;
}
.name-container h1 {
  font-size: 3rem;
  display: inline-flex;
  align-items: baseline;
  line-height: 0;
}
.name-container img {
  display: inline-block;
  position: relative;
  bottom: -10px;
  width: 5rem;
  height: auto;
  margin-right: -22px;
}
.name-container .intro-text {
  font-size: 1.5rem;
}

.intro-btn-container {
  display: flex;
  justify-content: left;
  align-items: center;
  gap: 2rem;
}

.btn-down-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, -50%);
}
.btn-down-container p {
  font-weight: 600;
}

.btn-down:hover,
.btn-down:active {
  filter: opacity(0.7);
  transition: all 0.3s ease-in-out;
}

@media (width <= 1120px) {
  .intro-container .name-container {
    padding: 1rem 0 0 0;
    flex-direction: column;
  }
  .intro-container .name-container .name-text {
    transform: translate(-48%, 10%);
  }
  .intro-container .intro-text {
    font-size: 1.175rem;
    max-width: 50ch;
  }
}
.project-wrapper {
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: center;
  padding: 4rem 0 0;
}

.outer-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-items: left;
}

.text-outer-wrapper {
  grid-column: 1/2;
  padding: 0 0.5rem 0 0;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  max-width: 500px;
}

.video-wrapper {
  grid-column: 2/2;
  color: #f0e9f2;
}
.video-wrapper video {
  width: 100%;
  height: auto;
  min-width: 450px;
}

.text-inner-wrapper {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.text-inner-wrapper button {
  align-self: flex-start;
  font-size: 1rem;
  font-weight: 500;
  font-style: italic;
}

.project-btn-container {
  display: flex;
  align-items: flex-start;
  gap: 2rem;
}

.project-underline {
  display: inline-block;
  border-bottom: #363d48 1px solid;
  width: 100%;
  margin-top: 4rem;
}

@keyframes slide {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-100%);
  }
}
.skills-container {
  overflow: hidden;
  white-space: nowrap;
  position: relative;
}

.skills-container::before {
  content: "";
  display: block;
  position: absolute;
  width: 50px;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 2;
  background: linear-gradient(to right, rgb(54, 61, 72), rgba(54, 61, 72, 0));
}

.skills-container::after {
  content: "";
  display: block;
  position: absolute;
  width: 50px;
  height: 100%;
  top: 0;
  right: 0;
  z-index: 2;
  background: linear-gradient(to left, rgb(54, 61, 72), rgba(54, 61, 72, 0));
}

